import Vue from 'vue'
import Router from 'vue-router'
import Hello from '../components/Hello.vue';


Vue.use(Router)

export default new Router({
  routes: [
      {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
        path: "/courses/:id",
        name: "Course Details",
        props: {editMode: true},
        component: "../components/CourseComponent"
      }, 
    {
        path: "/courses",
        name: "Courses",
       component: () => import("../components/CourseComponent")
        
      },
      
      {
        path: "/allCourses",
        name: "Course list",
        component: "../components/ListCoursesComponent"
      }
  ]
})